  height: 8px;
  /*border: black 2px;*/
  border-radius: 50%;
  animation: spinouter 1s cubic-bezier(0.08, 0.22, 0, 0.99) infinite;
  transition: border 0.2s cubic-bezier(0.66, 0.02, 0.88, 0.32);
  border-color: rgba(0, 0, 0, 0);
}

.inner {
  width: 6.2px; //30
