@keyframes spinouter {
  0% {
    transform: rotate(0deg);
    border: solid var(--accentD) .4px; //4
  }

  25% {
    transform: rotate(90deg);
    border: solid var(--blackfL) 2px; //20
  }

  50% {
    transform: rotate(180deg);
    border: solid var(--blackfL) 1px; //10
    border-radius: 0px;
  }

  75% {
    transform: rotate(270deg);
    border: solid var(--blackfL) 3px; //30
  }

  100% {
    transform: rotate(360deg);
    border: solid var(--blackfL) .4px; //4
  }
}

