  width: 30px;
  height: 30px;
  border-radius: 0 20px 0 0;
  display: grid;
  place-items: center;
  transform-origin: bottom left;
  transition: all 0.2s cubic-bezier(0.57, 0.07, 0.41, 1.95);
  pointer-events: auto;

  &:hover {
    transform: scale(1.1);
    border-radius: 0 10px 0 0;
  }

