  -webkit-animation: splash 0.8s var(--sleek);
  animation: splash 0.8s var(--sleek);
}

@-webkit-keyframes splash {
  from {
    transform: scale(0);
    opacity: 1;
    border-width: 2;
  }

  to {
    transform: scale(1);
    opacity: 0;
    border-width: 1;
  }
}

@keyframes splash {
  from {
    transform: scale(0);
    opacity: 1;
    border-width: 2;
  }

  to {
    transform: scale(1);
    opacity: 0;
    border-width: 1;
